<template>
  <v-container fluid class="down-top-padding">
    <v-row justify="center">
      <v-col cols="12" lg="8" md="8" sm="8" xs="11">
        <v-card>
          <v-card-text>
            <h3
              class="title blue-grey--text text--darken-2 font-weight-regular"
            >
              ویرایش محصول
            </h3>
            <h6 class="font-weight-light">
              ویرایش مشخصات محصول
            </h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <div>
              <span>شناسه محصولی: </span>
              <span>{{ this.fetchedProduct.id }}</span>
            </div>
            <div>
              <span>نام محصولی محصول: </span>
              <span>{{ this.fetchedProduct.title }}</span>
            </div>
          </v-card-text>
          <v-card-text>
            <v-row justify="center" class="pb-3">
              <ImageUploaderComponent
                v-for="i in inumber"
                :key="i"
                v-on:image-inputs="imageInput"
                v-on:remove-image-input="removeImageInput"
                :icon="i > 1 ? 'mdi-camera': undefined"
              >
              </ImageUploaderComponent>
              <v-card
                flat
                outlined
                style="width: 100px;"
                class="py-8 px-2 d-flex align-center"
              >
                <v-row justify="center">
                  <v-btn icon class="text-center" @click="inumber++">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </v-row>
              </v-card>
            </v-row>

            <v-row justify="center">
              <CategorySelectComponent
                v-on:on-select-category="onSelectCategory"
                :category="category"
              ></CategorySelectComponent>
            </v-row>
            <v-row justify="center">
              <v-col cols="12" sm="6" xs="8">
                <v-text-field
                  outlined
                  ref="name"
                  v-model="fetchedProduct.title"
                  :rules="[rules.required]"
                  label="نام"
                  placeholder="نام محصول"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" xs="4">
                <v-text-field
                  outlined
                  ref="number"
                  v-model="fetchedProduct.amount"
                  type="number"
                  :rules="[rules.required]"
                  label="تعداد"
                  placeholder="موجودی محصول"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-textarea
              filled
              ref="description"
              v-model="fetchedProduct.description"
              :rules="[rules.required, rules.max100]"
              label="توضیحات"
              placeholder="توضیحات محصول"
              counter="100"
              required
            ></v-textarea>
            <v-row justify="center">
              <v-col cols="12" sm="6" xs="8">
                <v-text-field
                  outlined
                  ref="price"
                  v-model="fetchedProduct.price"
                  type="number"
                  :rules="[rules.required]"
                  label="قیمت (تومان)"
                  placeholder="قیمت فروش محصول"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" xs="8">
                <v-text-field
                  outlined
                  ref="deadline"
                  v-model="fetchedProduct.deadline_date"
                  :rules="[rules.required]"
                  label="مهلت فروش"
                  placeholder="انتخاب تاریخ اتمام مهلت فروش"
                  id="my-custom-input"
                  required
                ></v-text-field>
                <date-picker
                  v-model="fetchedProduct.deadline_date"
                  format="jYYYY-jMM-jDD"
                  element="my-custom-input"
                />
              </v-col>
            </v-row>
            <v-row>
                <v-spacer></v-spacer>
                <v-btn
                @click="editProduct"
                class="text-capitalize mt-5 element-0"
                color="primary"
                >
                ثبت ویرایش
                </v-btn>
            </v-row>
          </v-card-text>
        </v-card>

        <AlertComponent
          v-if="alert_msg"
          :alertMsg="alert_msg"
          :alertType="alert_type"
        >
        </AlertComponent>

      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import authHeader from '../../middleware/authHeader';

export default {
  name: "ProductEdit",

  data: () => ({
    fetchedProduct: "",
    category: {},
    categories: [],
    title: "",
    price: "",
    realprice: "",
    amount: "",
    gaptime: "",
    deadline_date: "",
    cart_limit: "",
    send_types: [],
    selected_send_types: "",
    description: "",
    images: [],
    isNavSticky: true,
    inumber: 1,
    loading: false,
    alert_msg: '',
    alert_type: '',
    rules: {
      required: (value) => !!value || "فیلد ضروری!",
      min: (v) => v.length >= 8 || "حداقل ۸ کاراکتر!",
      min4: (v) => v.length >= 4 || !isNaN(v) || "حداقل ۴ کاراکتر!",
      min11: (v) => v.length == 11 || isNaN(v) || "شماره موبایل ۱۱ رقمی است!",
      max100: (v) => v ? v.length == 11 || isNaN(v) || "حداکثر 100 کاراکتر": "",
      emailMatch: (v) =>
        !v.includes("@") ||
        /\S+@\S+\.\S+/.test(v) ||
        "ایمیل را درست وارد کنید!",
    },
  }),
  components: {
    AlertComponent: () => import("@/components/sdComponents/AlertComponent"),
    ImageUploaderComponent: () =>
      import("@/components/commonComponents/ImageUploaderComponent.vue"),
    DatePicker: () => import("vue-persian-datetime-picker"),
    CategorySelectComponent: () =>
      import("@/components/commonComponents/CategorySelectComponent.vue"),
  },
  methods: {
    onSelectCategory (item) {
      this.category = item;
    },
    imageInput(filename) {
      this.images.push(filename);
    },
    removeImageInput(filename) {
      this.images.filter((e) => {
        e != filename;
      });
    },
    async editProduct() {
      this.alert_msg = "";
      this.alert_type = null;
      const productId = this.fetchedProduct.id;
      try {
        const result = await this.$axios.post("/graphql", {
          query: `mutation {
            productUpdate (input: {
                id: "${productId}"
                title: "${this.title}"
                realprice: "${this.realprice}"
                price: "${this.price}"
                amount: "${this.amount}"
                gaptime: "${this.gaptime}"
                deadline_date: "${this.deadline_date}"
                cart_limit: ${this.cart_limit}
                send_types: ${JSON.stringify(this.selected_send_types)}
                thumbnail: "${this.images[0]}"
                description: "${this.description}"
                categoryId: "${this.category.id}"
                images: "${this.images.toString()}"
              }) {
                id
                username
              }
          }`},
          authHeader(),
        );
        if (result.data.data.userUpdate.id) {
          this.alert_type = "success";
          this.alert_msg = "مشخصات محصول با موفقیت ویرایش شد.";
        } else {
          this.alert_type = "error";
          this.alert_msg = "اطلاعات وارد شده صحیح نیست.";
        }
      } catch (error) {
        console.error(error);
      }
    },

    fetchData: async function () {
      const productId = this.$route.params.productId;
        try {
          const result = await this.$axios.post("/graphql", {
            query: `query {
              productFind (id: "${productId}") {
                id
                pid
                title
                thumbnail
                price
                realprice
                description
                amount
                deadline_date
                createdAt
                categoryId {
                  id
                  name
                  persian_name
                }
                userId {
                  id
                  username
                  name
                  email
                  phone
                  avatar_url
                }
              }
            }`},
            authHeader()
          );
          if (result.data.data.errors) {
            this.alert_msg = result.data.data.errors[0].message;
          } else if (result.data.data.productFind) {
            this.fetchedProduct = result.data.data.productFind;
            this.category = this.fetchedProduct.categoryId;
          }
        } catch (error) {
          console.error(error);
        }
        try {
        const result = await this.$axios.post(
          "/graphql",
          {
            query: `query {
            sendFindByCategory (query: {
              categoryId: "${this.category}"
            }) {
              id
              send_type
              send_type_persian
              send_price
            }
          }`,
          },
          authHeader()
        );
        this.send_types = result.data.data.sendFindByCategory;
      } catch (error) {
        console.log(error);
      }
      try {
        const result = await this.$axios.post(
          "/graphql",
          {
            query: `query {
            categoryFindAll {
              id
              name
              persian_name
              categoryId {
                id
                name
                persian_name
              }
            }
          }`,
          },
          authHeader()
        );
        const scategories = result.data.data.categoryFindAll;
        this.categories = scategories;
      } catch (error) {
        console.log(error);
      }
    }
  },
  created () {
      this.fetchData()
  },
}
</script>
